  font-weight: 300;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-light.woff2") format("woff2"), url("../fonts/neuemachina-light.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-regular.woff2") format("woff2"), url("../fonts/neuemachina-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-medium.woff2") format("woff2"), url("../fonts/neuemachina-medium.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 900;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-ultrabold.woff2") format("woff2"), url("../fonts/neuemachina-ultrabold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Neue Machina", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0; }
  @media (min-width: 1024px) {
    .container {
      padding: 0 35px; } }

.header {
  display: block;
  padding: 25px 35px; }

.intro {
  height: calc(100vh - 80px);
  user-select: none; }
  @media (min-width: 1024px) {
    .intro {
      padding-bottom: 192px;
      user-select: auto; } }

.intro__title {
  margin: 0;
  font-size: 96px;
  font-weight: 400;
  line-height: 1;
  color: #2c39f2; }

.intro__text {
  font-size: 30px;
  font-weight: 400;
  line-height: 1.3;
  color: #ffffff; }
  @media (min-width: 1024px) {
    .intro__text {
      color: #000000; } }
  .intro__text p {
    margin: 0; }
  .intro__text p:not(:last-child) {
    margin-bottom: 16px; }

.intro__row {
  position: relative;
  display: grid;
  grid-template-columns: repeat(2, 100vw);
  grid-template-rows: 1fr;
  column-gap: 35px;
  height: 100%; }
  @media (min-width: 1024px) {
    .intro__row {
      grid-template-columns: repeat(2, 1fr);
      column-gap: 40px; } }

.intro__col {
  height: 100%;
  transition: transform 0.3s ease;
  cursor: pointer; }
  @media (min-width: 1024px) {
    .intro__col {
      transition: none;
      cursor: default; } }
  .intro__col--left {
    padding: 246px 35px 0; }
    @media (min-width: 1024px) {
      .intro__col--left {
        padding: 118px 0 0; } }
  .intro__col--right {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 161px 0 0;
    background-color: #2c39f2; }
    @media (min-width: 1024px) {
      .intro__col--right {
        position: relative;
        padding: 46px 0 0;
        background-color: #ffffff; } }
  .intro__col--hide {
    transform: translateX(-100%); }
    @media (min-width: 1024px) {
      .intro__col--hide {
        transform: none; } }

.intro__content {
  max-width: 352px;
  height: 100%;
  margin: 0 auto; }
  .intro__content svg {
    margin-bottom: 35px; }

.lizard__color-el-1,
.lizard__color-el-2,
.lizard__color-el-3 {
  fill: #ffffff; }

@media (min-width: 1024px) {
  .lizard__color-el-1 {
    fill: #2c39f2; } }

@media (min-width: 1024px) {
  .lizard__color-el-2 {
    fill: #9627a3; } }

@media (min-width: 1024px) {
  .lizard__color-el-3 {
    fill: #ff1553; } }

